.accessories {
  margin-left: auto;
  margin-right: auto;
  min-width: 320px;
  padding-top: 40px;
  padding-bottom: 48px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  background-image: url(../images/accessories/bg-mob.jpg);

  @include retina {
    & {
      background-image: url(../images/accessories/bg-mob-2x.jpg);
    }
  }

  @include tablet {
    padding-top: 120px;
    padding-bottom: 120px;
    background-image: url(../images/accessories/bg-tab.jpg);

    @include retina {
      background-image: url(../images/accessories/bg-tab-2x.jpg);
    }
  }

  @include desktop {
    background-image: url(../images/accessories/bg-desktop.jpg);

    @include retina {
      background-image: url(../images/accessories/bg-desktop-2x.jpg);
    }
  }
}

